




















































































































var userLoginInfo;
import myDrawer from '@/components/my-animation.vue';
import pay from '@/components/pay/pay.vue'
export default {
	data() {
		return {
			orderData: [],
			order_trade_no: '',
			modalHidden: true,
			modalHidden2: true,
			openAttr: false,
			isdefault: true,
			checked: 1,
			radioItems: [{
					name: '微信支付',
					value: '1',
					icon: 'http://static.lamamuying.com/static/images/wx_icon.png',
					checked: 'true'
				},
				{
					name: '支付宝支付',
					icon: 'http://static.lamamuying.com/static/images/alipay_icon.png',
					value: '2'

				}
			],
		};
	},
	onLoad: function(params) {
		if (!params.order_trade_no) {
			uni.showToast({
				title: '参数错误',
				icon: 'none'
			});
		} else {
			this.order_trade_no = params.order_trade_no;
			this.getOrder();
		}
	},
	onShow() {},
	components: {
		myDrawer,
		pay
	},
	methods: {
		// 小程序中微信付款
		showWeixin: function() {
			uni.request({
				url: this.apiUrl + 'pay/mpPay',
				method: 'GET',
				data: {
					type: 'order',
					order_trade_no: this.order_trade_no
				},
				success: res => {
					if (res.data.code == 0) {
						let payData = res.data;
						this.payParams = payData.data;
						uni.requestPayment({
							provider: 'wxpay',
							timeStamp: this.payParams.timeStamp,
							nonceStr: this.payParams.nonceStr,
							package: this.payParams.package,
							signType: this.payParams.signType,
							paySign: this.payParams.paySign,
							success: function(res) {
								console.log('success:' + JSON.stringify(res));
							},
							fail: function(err) {
								console.log('fail:' + JSON.stringify(err));
							}
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// app付款
		showApp: function() {
			// 显示弹出框
			this.openAttr = true;
			this.isdefault = false;
		},
		// 确认支付 支付宝或者微信支付
		goAppPay: function() {
			let data = {
				orderInfo: this.orderData.amount
			}
			if (this.checked == 1) {
				uni.showLoading({
					title:'加载中...'
				})
				uni.request({
					url: this.apiUrl + 'pay/appWechat',
					method: 'GET',
					data: {
						order_trade_no: this.order_trade_no,
						type: 'order'
					},
					success: res => {
						if (res.data.code == 0) {
							uni.requestPayment({
								provider: 'wxpay',
								orderInfo: res.data.data, //微信、支付宝订单数据
								success: payRes => {
									// console.log('success:' + JSON.stringify(payRes));
									uni.showToast({
										title:'支付成功'
									});
									uni.reLaunch({
										url : '../order/order'
									})
								},
								fail: payErr => {
									console.log('fail:' + JSON.stringify(payErr));
								}
							});
						}
					},
					fail: () => {},
					complete: () => {
						uni.hideLoading();
					}
				});
			} else {
				uni.showLoading({
					title:'加载中...'
				})
				uni.request({
					url: this.apiUrl + 'pay/appAlipay',
					method: 'GET',
					data: {
						order_trade_no: this.order_trade_no,
						type: 'order'
					},
					success: res => {
						if (res.data.code == 0) {
							//console.log(res.data.data);
							uni.requestPayment({
								provider: 'alipay',
								orderInfo: res.data.data, //微信、支付宝订单数据
								success: payRes => {
									// console.log('success:' + JSON.stringify(payRes));
									uni.showToast({
										title:'支付成功'
									});
									uni.reLaunch({
										url : '../order/order'
									})
								},
								fail: payErr => {
									console.log('fail:' + JSON.stringify(payErr));
								}
							});
						}
					},
					fail: () => {},
					complete: () => {
						uni.hideLoading();
					}
				});
			}
		},			
		// 获取支付方式
		radioChange: function(e) {
			this.checked = e.detail.value;
			this.radioItems.forEach((radio)=>{
				if(radio.value == this.checked){
					radio.checked = true;
				}else{
					radio.checked = false;
				}
			});
		},
		noTitlemodalTap: function(e) {
			uni.showModal({
				content: '要取消吗？',
				success: res => {
					if (res.confirm) {
						console.log('用户点击确定');
						// this.orderData = [],
						uni.redirectTo({
							url: '../order/order'
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		// 购买商品--订单
		getOrder() {
			userLoginInfo = this.userLoginInfo('../order/firm_order', 1);
			uni.showLoading({
				title: '数据加载中..',
				mask: true
			});
			uni.request({
				url: this.apiUrl + 'user/getOrderView',
				method: 'GET',
				data: {
					uid: userLoginInfo.id,
					order_trade_no: this.order_trade_no
				},
				success: res => {
					if (res.data.code == 0) {
						this.orderData = res.data.data;
						// console.log(this.orderData);
					}
				},
				fail: () => {},
				complete: () => {
					uni.hideLoading();
				}
			});
		},
		// 关闭规格弹窗
		closeAttr() {
			this.openAttr = false;
			this.isdefault = true;
		},

	}
};
